body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f7f9fc;
  color: #333;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.settings-container {
  max-width: 480px;
  margin: 3rem auto;
  background: #fff;
  padding: 2.5rem 3rem;
  border-radius: 12px;
  box-shadow: 0 6px 18px rgba(0,0,0,0.1);
}

h2 {
  margin-bottom: 1.8rem;
  font-weight: 700;
  color: #222;
  text-align: center;
}

form {
  display: flex;
  flex-direction: column;
  gap: 1.4rem;
}

label {
  font-weight: 600;
  font-size: 1rem;
  color: #444;
  display: flex;
  flex-direction: column;
  gap: 0.35rem;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="file"] {
  padding: 0.65rem 0.8rem;
  font-size: 1rem;
  border-radius: 8px;
  border: 1.8px solid #cbd5e1;
  outline-offset: 2px;
  transition: border-color 0.25s ease;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="password"]:focus,
input[type="file"]:focus {
  border-color: #4caf50;
  outline: none;
}

.preview-img {
  margin-top: 0.6rem;
  width: 110px;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.12);
  align-self: start;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-weight: 600;
  font-size: 1rem;
  user-select: none;
}

.checkbox-label input {
  width: 18px;
  height: 18px;
  cursor: pointer;
}

button {
  margin-top: 1.6rem;
  padding: 0.85rem 0;
  font-size: 1.1rem;
  font-weight: 700;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.save-btn {
  background-color: #4caf50;
  color: white;
}

.save-btn:hover {
  background-color: #43a047;
}

.delete-btn {
  background-color: #e53935;
  color: white;
  margin-top: 0.8rem;
}

.delete-btn:hover {
  background-color: #d32f2f;
}

/* Dark mode overrides */
body.dark-mode {
  background-color: #121212;
  color: #eee;
}

body.dark-mode .settings-container {
  background-color: #1e1e1e;
  box-shadow: 0 6px 18px rgba(255,255,255,0.08);
}

body.dark-mode input[type="text"],
body.dark-mode input[type="email"],
body.dark-mode input[type="password"],
body.dark-mode input[type="file"] {
  background-color: #2c2c2c;
  color: #eee;
  border-color: #555;
}
